<!-- 搜索页 ----wzh  -->
<template>
  <div class="search">
    <div class="search-left">
      <keyboard ref="kbc" v-on:pressDown="keepDo"></keyboard>
    </div>
    <div class="search-right">
      <template v-if="isHotSearch">
        <div class="hot-search">
          <div class="hot-search-top">
            <span>大家都再搜：</span>
            <ul>
              <li>
                <div>德玛西亚杯</div>
              </li>
              <li>
                <div>李白</div>
              </li>
              <li>
                <div>狼人杀</div>
              </li>
            </ul>
          </div>
          <div class="hot-search-content">
            <h3>热门榜单</h3>
            <ul>
              <template v-for="(item,index) in this.recommendData">
                <li :key="index" :id="('list-index-'+index)" :class="{'active':index===hotNum}" v-if="index<3">
                  <img :src=(item.elementImg) alt="">
                </li>
              </template>
            </ul>
          </div>
        </div>
      </template>
      <template v-else>
        <div class="user-search" v-if="haveSearchData">
          <div class="user-search-top">
            <ul>
              <li>
                <div>
                  <p>巅峰对决</p><span>({{type[1] && type[1].resultList.length || null}})</span>
                </div>
              </li>
              <li>
                <div>
                  <p>新游速讯</p><span>({{type[2] && type[2].resultList.length || null}})</span>
                </div>
              </li>
              <li>
                <div>
                  <p>主播解说</p><span>({{ type[3] && type[3].resultList.length || null}})</span>
                </div>
              </li>
              <li>
                <div>
                  <p>电竞综艺</p><span>({{type[4] && type[4].resultList.length || null}})</span>
                </div>
              </li>
            </ul>
          </div>
          <div class="user-search-content">
            <div class="next-L" v-show="pageNum>0"></div>
            <div class="next-R" v-show="pageNum<total&&pageNum!=total-1"></div>
            <ul>
              <li v-for="(item,index) in this.top[pageNum]" :key="index" :id="('list-index-'+index)">
                <div class="acc" :class="{'active':index===searchNum}"></div>
                <img :src='top[pageNum][index].elementImgH' alt="" border="0">
                <!-- <span>{{item.elementName}}</span> -->
              </li>
            </ul>
          </div>
          <div class="current"><span>{{this.pageNum+1}}/{{total}}</span> </div>
        </div>
      </template>
    </div>
    <keyDo ref="keydo" v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>
</template>

<style scoped>
  .search {
    width: 1280px;
    height: 720px;
    position: relative;
    background: url("../../assets/search/search-bg.png");
    overflow:hidden
  }

  .search-left {
    width: 380px;
    float: left;
  }

  .search-right {
    width: 900px;
    height: 720px;
    padding-left: 380px
  }

  .hot-search {
    font-size: 20px;
    position: relative;
  }

  .hot-search .hot-search-top {
    position: absolute;
    top: 60px;
    left: 20px;
    width: 800px;

  }

  .hot-search-top span {
    float: left;
    line-height: 42px;
  }

  .hot-search-top ul li {
    float: left;
    width: 120px;
    height: 42px;
    margin-left: 20px;
    background-color: #5C8AC0;
    line-height: 42px;
    text-align: center;
    transform: skewX(-30deg);
  }

  .hot-search-top ul li div {
    transform: skewx(30deg);
  }

  .hot-search .hot-search-content {
    width: 800px;
    height: 720px;
    overflow: hidden;
    position: absolute;
    top: 140px;
    left: 20px;
  }

  .hot-search-content h3 {
    font-weight: 400;
    font-size: 20px;
    opacity: 0.4;
  }

  .hot-search-content ul {
    width: 760px;
    margin-top: 60px;
    margin-left: 30px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: flex-end;
  }

  .hot-search-content ul li {
    width: 215px;
    height: 325px;
    /* margin-left: 25px; */
    text-align: center;
  }

  .hot-search-content ul li img {
    width: 100%;
    height: 100%;
    /* background: url("../../assets/search/img_215x325.png") no-repeat; */
    background-size: cover;
  }

  .hot-search-content ul li.active {
    width: 260px;
    height: 395px;
  }

  .hot-search .current {
    position: absolute;
    top: 640px;
    right: 40px;
  }

  .user-search {
    font-size: 20px;
    position: relative;
  }

  .user-search .user-search-top {
    position: absolute;
    top: 50px;
    left: 90px;
    width: 800px;
  }

  .user-search-top ul li {
    float: left;
    width: 120px;
    height: 57px;
    margin-left: 20px;
    background-color: #5C8AC0;
    text-align: center;
    transform: skewX(-25deg);
  }

  .user-search-top ul li div {
    transform: skewx(25deg);
  }

  .user-search .current {
    position: absolute;
    top: 640px;
    right: -12px;
    width: 120px;
    height: 58px;
    margin-left: 20px;
    background: url(../../../static/images/history/861.png) no-repeat;
    line-height: 58px;
    text-align: center;
    transform: skewX(-20deg);
  }

  .user-search .current span {
    transform: skewX(20deg);
  }

  .user-search .next-L {
    width: 66px;
    height: 66px;
    background: url('../../assets/search/icons_dark_next.png');
    position: absolute;
    top: 225px;
    left: -60px;
  }

  .user-search .next-R {
    width: 66px;
    height: 66px;
    background: url('../../assets/search/icons_dark_next.png');
    position: absolute;
    top: 225px;
    right: -50px;
    transform: rotate(180deg)
  }

  .user-search-content {
    position: absolute;
    top: 130px;
    left: 60px;
  }

  .user-search-content ul {
    width: 780px;
    height: 500px;
    /* display: flex;
    flex-flow: row wrap; */
    overflow: hidden;
  }

  .user-search-content ul li {
    text-align: center;
    width: 231px;
    height: 130px;
    margin-left: 20px;
    margin-top: 30px;
    position: relative;
    float: left;
  }
.user-search-content ul li .acc{
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
}
  .user-search-content ul li .acc.active {
    background: url(../../../static/images/special/915.png);
    background-size: cover;
  }

  .user-search-content ul li img {
    width: 100%;
    height: 100%;
    background-size: cover;
  }

  .noData {
    font-size: 30px;
    position: relative;
    top: 360px;
    left: 340px
  }
</style>

<script>
import keyboard from './keyboard.vue';
import c from '../../js/common.js';
import {
  HTTPUtil as api
} from '../../fetch/api.js';
import {
  mapGetters
} from 'vuex';
import {bi} from '../../js/logApi.js'
import Toast from '../../tool/toast/toast.js'
export default {
  name: 'search',
  data () {
    return {
      isShow: false,
      isHotSearch: true,
      hotNum: -1, // 推荐资产编号
      searchNum: -1, // 搜索资产编号
      keyWord: '',
      recommendData: [],
      searchData: [],
      type: [],
      top: [],
      haveSearchData: true,
      total: 0, // 搜索结果总页数
      pageNum: 0, // 当前搜索页码
      pageIndex: 0
    }
  },
  components: {
    keyboard
  },
  created () {
    this.getSearchRecommend();
  },
  mounted (){
    this.newJump();
  },
  activated () {
    this.startTimes = new Date() - 0;
    if (this.$route.params.type == 'search'){
      // 组件被缓存时需要重新进行初始化
      this.isShow = false;
      this.isHotSearch = true;
      this.$refs.kbc.keyword = '';
      this.$refs.kbc.getKeyListen(0);
    }
    this.newJump()
  },
  computed: {
    ...mapGetters([
      'behindParams'
    ])
  },
  watch: {
    keyWord () {
      if (this.keyWord.length === 0) {
        this.isHotSearch = true
      } else {
        this.isHotSearch = false
        this.pageNum = 0;
        this.pageIndex = 0;
        this.getSearchData(this.keyWord, this.pageIndex)
      }
    }

  },
  methods: {
    getSearchRecommend () {
      console.log('请求搜索推荐数据');
      const url = api.getSearchHot + this.behindParams
      api.jsonp(url, (res) => {
        this.recommendData = res.data.hotAssetList
      })
    },
    getSearchData (keyWord, pageIndex) {
      var word = keyWord.toLowerCase();
      const url = api.getSearchData(word);
      console.log(url);
      api.jsonp(url, (res) => {
        console.log(res);
        if (res.data != null) {
          this.type = res.data;
          var jsonp = [];
          this.searchData = jsonp.concat(res.data[1].resultList, res.data[2].resultList, res.data[3].resultList, res.data[4].resultList);
          if (this.searchData.length <= 0) {
            let w = this.keyWord.split('').join('|');
            this.haveSearchData = false;
            this.isHotSearch = true;
            Toast({'message': '无搜索内容,为您展示推荐内容', 'position': 'bottom', 'duration': 2000});
            let biObj = {
              result: '0',
              keyword: w,
              click_type: '',
              cid: ''
            }
            bi.keyDoSearch(biObj);
          } else {
            this.haveSearchData = true;
          }
        } else {
          this.haveSearchData = false;
        }
        this.total = Math.ceil(this.searchData.length / 9);
        this.top = this.split_array(this.searchData, 9);
      })
    },
    keepDo (isShow, keyWord, position) {
      if (isShow) { // 键值在左
        if (position === 'search') {
          this.isHotSearch = false;
          this.keyWord = keyWord
        }
      } else { // 键值向右
        this.isShow = true;
        if (this.isHotSearch) {
          this.hotNum = 0;
        } else {
          this.searchNum = 0;
        }
      }
    },
    keyCode (kc) {
      if (kc === 'down') {
        this.down();
      } else if (kc === 'up') {
        this.up();
      } else if (kc === 'left') {
        this.left();
      } else if (kc === 'right') {
        this.right();
      } else if (kc === 'KeyEnter') {
        this.KeyEnter();
      } else if (kc === 'KeyBack') {
        this.isShow = false;
        this.hotNum = this.searchNum = -1
        this.$refs.kbc.getKeyListen();
      }
    },
    up () {
      if (this.isHotSearch) return;
      if (this.searchNum <= 2){

      } else {
        this.searchNum -= 3;
      }
    },
    down () {
      if (this.isHotSearch) return;
      if (this.searchNum == 6 || this.searchNum == 7 || this.searchNum == 8) return;
      this.searchNum += 3;
      if (this.searchNum >= this.top[this.pageNum].length - 1) {
        this.searchNum = this.top[this.pageNum].length - 1;
      }
    },
    left () {
      if (this.isHotSearch) {
        if (this.hotNum === 0) {
          this.isShow = false;
          this.$refs.kbc.getKeyListen();
        }
        this.hotNum--
      } else { // 搜索资产
        if ((this.searchNum) % 3 === 0) { // 向左
          if (this.pageNum === 0) { // 第一页的最左边
            this.searchNum = -1;
            this.isShow = false;
            this.$refs.kbc.getKeyListen();
          } else { // 非第一页最左边
            this.pageNum--
            this.$nextTick(() => {
              this.searchNum = 0
            })
          }
        } else {
          this.searchNum--
        }
      }
    },
    right () {
      if (this.isHotSearch) {
        if (this.hotNum === 2) return
        this.hotNum++
      } else { // 搜索资产
        if (this.searchNum == 2 || this.searchNum == 5 || this.searchNum == 8 || this.searchNum == this.top[this.pageNum].length - 1) {
          if (this.pageNum < this.total - 1) {
            this.pageNum++;
            this.$nextTick(() => {
              this.searchNum = 0
            })
          }
        } else {
          this.searchNum++
        }
      }
    },
    KeyEnter () {
      let url = '';
      let layType = '';
      let elementType = '';
      c.setParentPageType('0201');
      c.setParentPageId('100-1');
      c.setJump();
      let clickType = '';
      let cid = '';
      let recmd_id = '';
      if (this.isHotSearch){
        recmd_id = '1';
        localStorage.setItem('BI_recmd_id', 1);
        url = this.recommendData[this.hotNum].jsonUrl;
        layType = this.recommendData[this.hotNum].layout;
        elementType = this.recommendData[this.hotNum].elementType;
        // if( this.recommendData[this.hotNum].layout === 'Detail_News' ){
        //   clickType = '1'
        // } else {
        //   clickType = '3'
        // }
        let t = this.recommendData[this.hotNum].elementType;
        if (t == 1){
          clickType = 1;
        } else if (t == 4 || t == 5) {
          clickType = 2;
        } else {
          clickType = 3
        }
        cid = this.recommendData[this.hotNum].elementId
        // 热门推荐位点击日志
        let biObj = {
          result: '2',
          keyword: '',
          click_type: clickType,
          cid: cid
        }
        bi.keyDoSearch(biObj);
        let recBiObj = {
          pos_id: '',
          recmd_id: '1',
          page_type: '0201',
          page_id: '100-1',
          click_type: clickType,
          cid: cid
        }
        bi.jumpRecommend(recBiObj);
      } else {
        let w = this.keyWord.split('').join('|');
        localStorage.setItem('BI_recmd_id', '');
        cid = this.top[this.pageNum][this.searchNum].elementId;
        // if( this.top[this.pageNum][this.searchNum].layout === 'Detail_News' ){
        //    clickType = '1'
        // } else {
        //   clickType = '3'
        // }
        let t = this.top[this.pageNum][this.searchNum].elementType;
        if (t == 1){
          clickType = 1;
        } else if (t == 4 || t == 5) {
          clickType = 2;
        } else {
          clickType = 3
        }
        let biObj = {
          result: '1',
          keyword: w,
          click_type: clickType,
          cid: cid
        }
        bi.keyDoSearch(biObj);
        recmd_id = '';
        url = this.top[this.pageNum][this.searchNum].jsonUrl;
        layType = this.top[this.pageNum][this.searchNum].layout;
        elementType = this.top[this.pageNum][this.searchNum].elementType;
      }
      let biObj = {
        stay_time: new Date() - this.startTimes,
        recmd_id: recmd_id,
        pos_id: null,
        page_type: '0201',
        page_id: '100-1',
        click_type: '1',
        click_id: '100-1'
      }
      // bi.jump(biObj);
      c.routerSkip(url, elementType.toString(), layType, {type: 'search'}, this.$router);
    },
    split_array (arr, len){
      var a_len = arr.length;
      var result = [];
      for (var i = 0; i < a_len; i += len) {
        result.push(arr.slice(i, i + len));
      }
      return result;
    },
    newJump (){
      if (c.isJump() == 'jump'){
        c.removeJump();
        let jumpObj = {
          page_type: '0201',
          page_id: '100-1',
          parent_page_type: c.getParentPageType(),
          parent_page_id: c.getParentPageId()
        }
        bi.newJump(jumpObj)
      }
    }
  }
}
</script>



